<script lang="ts" setup>
import { Refresh, Search, Close } from '@element-plus/icons-vue';
import { ref, onMounted, defineAsyncComponent, nextTick } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import request from '@/utils/request';

// ÂºïÂÖ•Ëá™ÂÆö‰πâÊèí‰ª∂Âíå API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// ÂØºÂÖ•ËØ¶ÊÉÖÈ°µÈù¢
const DetailForm = defineAsyncComponent(() => import('./detail.vue'));
// ÂØºÂÖ•ËØ¶ÊÉÖÈ°µÈù¢
const detailFormRef = ref(null);
const openDetailFrom = (row = {}) => {
  console.log(row);
  // ‰ΩøÁî® nextTick Á°Æ‰øùÁªÑ‰ª∂Â∑≤ÁªèÊåÇËΩΩ
  nextTick(() => {
    if (detailFormRef.value) {
      detailFormRef.value.open({ ...row}); // ‰º†ÈÄí `row` ‰Ωú‰∏∫ÂèÇÊï∞
    } else {
      console.error("DetailForm ÁªÑ‰ª∂Êú™Ê≠£Á°ÆÊåÇËΩΩÔºÅ");
    }
  });
};
//
// // ‰ΩøÁî®Ë°®Ê†ºÊèí‰ª∂Êù•ÁÆ°ÁêÜË°®Ê†ºÁöÑÁä∂ÊÄÅ„ÄÅÊï∞ÊçÆÁ≠â
// const {
//   handleSelectionChange,
//   hasSelectedRows,
//   isVisible: showSearch,
//   toggleVisibility: toggleSearch,
//   loadData,
//   data,
//   refreshData,
//   handleSizeChange,
//   handleCurrentChange,
//   searchParams,
//   resetSearchParams
// } = useTableDataPlugin("/api/v1/admin/order");
const {
  handleSelectionChange,
  hasSelectedRows,
  isVisible: showSearch,
  toggleVisibility: toggleSearch,
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams
} = useTableDataPlugin("/api/v1/admin/order");
// Á°Æ‰øùÊêúÁ¥¢ÂèÇÊï∞ÊúâÊ≠£Á°ÆÁöÑÁªìÊûÑ
if (!(searchParams.value as any).search) {
  (searchParams.value as any).search = {};
}

// ÂàùÂßãÂåñÊêúÁ¥¢ÂèÇÊï∞
const initSearchParams = () => {
  if (!(searchParams.value as any).search) {
    (searchParams.value as any).search = {
      order_no: '',
      merchant_order_no: '',
      merchant_id: '',
      status: '',
      product_name: '',
      start_time: '',
      end_time: ''
    };
  }
};

// ÊêúÁ¥¢Ë°®ÂçïÊï∞ÊçÆ
const searchForm = ref({
  order_no: '',
  merchant_order_no: '',
  merchant_id: '',
  status: '',
  product_name: '',
  timeRange: null as [Date, Date] | null
});


// ÊêúÁ¥¢Âä†ËΩΩÁä∂ÊÄÅ
const searchLoading = ref(false);


// Â§ÑÁêÜÊêúÁ¥¢
const handleSearch = async () => {
  searchLoading.value = true;
  try {
    // Â§ÑÁêÜÊó∂Èó¥ËåÉÂõ¥
    let searchData: any = { ...searchForm.value };
    if (searchData.timeRange && searchData.timeRange.length === 2) {
      searchData.start_time = searchData.timeRange[0];
      searchData.end_time = searchData.timeRange[1];
    }
    delete searchData.timeRange;
    delete searchData.quickTime;
    
    // Â∞ÜÊêúÁ¥¢Ë°®ÂçïÊï∞ÊçÆÂêåÊ≠•Âà∞searchParams
    (searchParams.value as any).search = searchData;
    await refreshData();
  } catch (error) {
    console.error('ÊêúÁ¥¢Â§±Ë¥•:', error);
    ElMessage.error('ÊêúÁ¥¢Â§±Ë¥•ÔºåËØ∑ÈáçËØï');
  } finally {
    searchLoading.value = false;
  }
};

// Â§ÑÁêÜÈáçÁΩÆ
const handleReset = () => {
  searchForm.value = {
    order_no: '',
    merchant_order_no: '',
    merchant_id: '',
    status: '',
    product_name: '',
    timeRange: null
  };
  (searchParams.value as any).search = { ...searchForm.value };
  refreshData();
};

// Âú®ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊêúÁ¥¢ÂèÇÊï∞ÂíåÂä†ËΩΩÊï∞ÊçÆ
onMounted(() => {
  initSearchParams();
  loadData();
});

// Ëé∑ÂèñËÆ¢ÂçïÁä∂ÊÄÅÊñáÊú¨
const getStatusText = (status: number) => {
  const statusMap: { [key: number]: string } = {
    1: 'ÂæÖÊîØ‰ªò',
    2: 'ÊîØ‰ªò‰∏≠',
    3: 'ÊîØ‰ªòÊàêÂäü',
    4: 'ÊîØ‰ªòÂ§±Ë¥•',
    5: 'Â∑≤ÈÄÄÊ¨æ',
    6: 'Â∑≤ÂÖ≥Èó≠'
  };
  return statusMap[status] || 'Êú™Áü•Áä∂ÊÄÅ';
};

// Ëé∑ÂèñËÆ¢ÂçïÁä∂ÊÄÅÊ†áÁ≠æÁ±ªÂûã
const getStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    1: 'warning',
    2: 'primary',
    3: 'success',
    4: 'danger',
    5: 'info',
    6: 'info'
  };
  return typeMap[status] || 'info';
};

// Ëé∑ÂèñÈÄöÁü•Áä∂ÊÄÅÊñáÊú¨
const getNotifyStatusText = (status: number) => {
  const statusMap: { [key: number]: string } = {
    0: 'Êú™ÈÄöÁü•',
    1: 'ÈÄöÁü•ÊàêÂäü',
    2: 'ÈÄöÁü•Â§±Ë¥•'
  };
  return statusMap[status] || 'Êú™Áü•Áä∂ÊÄÅ';
};

// Ëé∑ÂèñÈÄöÁü•Áä∂ÊÄÅÊ†áÁ≠æÁ±ªÂûã
const getNotifyStatusTagType = (status: number) => {
  const typeMap: { [key: number]: string } = {
    0: 'warning',
    1: 'success',
    2: 'danger'
  };
  return typeMap[status] || 'info';
};

// Â§ÑÁêÜÈÄÄÊ¨æ
const handleRefund = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `Á°ÆÂÆöË¶ÅÈÄÄÊ¨æËÆ¢Âçï ${row.order_no} ÂêóÔºü`,
      'Á°ÆËÆ§ÈÄÄÊ¨æ',
      {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning',
      }
    );

    // Ë∞ÉÁî®ÈÄÄÊ¨æAPI
    const { refundOrder } = await import('@/api/orderManagement.ts');
    const res = await refundOrder('/api/v1/admin/order/refund', {
      order_id: row.id,
      reason: 'ÁÆ°ÁêÜÂëòÈÄÄÊ¨æ'
    });

    if (res.status) {
      ElMessage.success('ÈÄÄÊ¨æÁî≥ËØ∑Â∑≤Êèê‰∫§');
      refreshData();
    } else {
      ElMessage.error(res.message);
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('ÈÄÄÊ¨æÁî≥ËØ∑Â§±Ë¥•');
    }
  }
};

// Â§ÑÁêÜË°•Âçï
const handleReissue = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `Á°ÆÂÆöË¶ÅË°•Âçï ${row.order_no} ÂêóÔºü`,
      'Á°ÆËÆ§Ë°•Âçï',
      {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning',
      }
    );
    
    // Ë∞ÉÁî®Ë°•ÂçïAPI
    const result = await request.post('/api/v1/admin/order/reissue', {
      ids: [row.id]
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('Ë°•ÂçïÊàêÂäü');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('Ë°•ÂçïÂ§±Ë¥•');
  }
};

// Â§ÑÁêÜÂõûË∞É
const handleCallback = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `Á°ÆÂÆöË¶ÅÈáçÊñ∞ÂõûË∞É ${row.order_no} ÂêóÔºü`,
      'Á°ÆËÆ§ÂõûË∞É',
      {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning',
      }
    );
    
    // Ë∞ÉÁî®ÂõûË∞ÉAPI
    const result = await request.post('/api/v1/admin/order/callback', {
      ids: [row.id]
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('ÂõûË∞ÉÊàêÂäü');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('ÂõûË∞ÉÂ§±Ë¥•');
  }
};

// ÊâπÈáèË°•Âçï
const handleBatchReissue = async () => {
  if (!hasSelectedRows.value) {
    ElMessage.warning('ËØ∑ÈÄâÊã©Ë¶ÅË°•ÂçïÁöÑËÆ¢Âçï');
    return;
  }
  
  try {
    await ElMessageBox.confirm(
      `Á°ÆÂÆöË¶ÅÊâπÈáèË°•ÂçïÈÄâ‰∏≠ÁöÑËÆ¢ÂçïÂêóÔºü`,
      'Á°ÆËÆ§ÊâπÈáèË°•Âçï',
      {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning',
      }
    );
    
    // Ë∞ÉÁî®Ë°•ÂçïAPIÔºàÁªü‰∏Ä‰ΩøÁî®idsÂèÇÊï∞Ôºâ
    const result = await request.post('/api/v1/admin/order/reissue', {
      ids: (data.value as any).selectedRows?.map((row: any) => row.id) || []
    });
    
    if ((result as any).code === 200) {
      ElMessage.success('ÊâπÈáèË°•ÂçïÊàêÂäü');
      refreshData();
    } else {
      ElMessage.error((result as any).msg);
    }
  } catch (error) {
    ElMessage.error('ÊâπÈáèË°•ÂçïÂ§±Ë¥•');
  }
};

// ÊâπÈáèÂõûË∞É
const handleBatchCallback = async () => {
  if (!hasSelectedRows.value) {
    ElMessage.warning('ËØ∑ÈÄâÊã©Ë¶ÅÂõûË∞ÉÁöÑËÆ¢Âçï');
    return;
  }
  
  try {
